.formOutside {
	height: calc(100vh);
	background-color: #7cbbc1;

	.form {
		.formContainer {
			font-size: 18px;
			border-radius: 10px;
			box-shadow: 1px 10px 29px 0px rgba(0,0,0,0.75);
			background-color: white;
			max-width: 900px;
			margin: 5% auto;
			padding: 10px 10px;

			.heading {
				font-size: 20px;
				font-weight: 700;
				padding: 10px 0;
			}

			.title {
				font-size: 16px;
				border-radius: 5px;
				width: 50%;
				padding: 5px 10px;
			}

			.timeContainer {
				display: flex;
				margin: 15px 0;

				.endTime {
					margin-left: 50px;
				}
			}

			.userContainer {
				.userWrapper {
					display: flex;
					flex-wrap: wrap;
					border: 2px solid black;
					border-radius: 10px;
					padding: 10px 10px;
					min-height: 50px;
					.noPar{
						font-size:15px;
					}
					.user {
						font-size:16px;
						background-color: #dedede;
						border-radius: 10px;
						padding: 2px 5px;
						color: black;
						margin: 2px 5px;
						display: flex;
						align-items: center;
						justify-content: center;

						.icon {
							display: flex;
							align-items: center;
							justify-content: center;
							cursor: pointer;
							background-color: #bcb8b8;
							border-radius: 100%;
							margin-left: 5px;
						}
					}
				}

				.chooseBtn {
					margin: 10px 0;
					border-radius: 10px;
					padding: 5px 10px;
					border: none;
					font-size: 16px;
					font-weight: 500;
					background-color: rgb(116, 206, 206);
					
					&:hover {
						background-color: rgb(80, 151, 151);
					}
				}
			}
			.chooseContainer{
				display: flex;
				justify-content: center;
				
				.submit {
					margin: 10px 0;
					border-radius: 10px;
					padding: 5px 10px;
					border: none;
					font-size: 16px;
					font-weight: 500;
					background-color: rgb(116, 206, 206);
					
					&:hover {
						background-color: rgb(80, 151, 151);
					}
				}
			}
		}
		
	}
}